html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 62.5%;
  font-family: "Oswald";
}

p {
  margin: 0;
  font-size: 1.6rem;
  font-weight: 300;
  text-align: center;
}

.contenedor {
  margin: 0 auto;
  max-width: 120 rem;
  width: 95%;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
  margin: 0;
}

h2 {
  font-size: 3.6rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h1, h2, h3 {
  text-align: center;
  font-weight: bold;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 0 5rem;
  align-items: center;
  background-color: #d4d955;
  flex-direction: column;
  margin-bottom: 2rem;
}
.header img {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
  }
  .header img {
    width: 30%;
    height: auto;
  }
}

nav {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-top: 1.2rem;
}
@media (min-width: 1200px) {
  nav {
    flex-direction: row;
  }
}
nav .link {
  font-size: 1.6rem;
  margin-right: 1rem;
  color: #648c27;
  margin-top: 1rem;
  text-align: center;
}
nav .link:hover {
  font-size: 1.8rem;
  color: #0d0d0d;
}

.imagen-main {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
}
.imagen-main img {
  width: 100%;
  height: 30rem;
  overflow: hidden;
  object-fit: cover;
  margin-bottom: 5rem;
}
.imagen-main .darken-image {
  filter: brightness(30%);
}
.imagen-main p {
  margin-top: 1.5rem;
}

.button {
  font-size: 2rem;
  margin: 3rem auto 0 auto;
  text-align: center;
  background-color: #648c27;
  color: #F0F2a0;
  padding: 1.5rem;
  border-radius: 1.5rem;
  text-transform: uppercase;
}
.button:hover {
  background-color: #d4d955;
  color: #0d0d0d;
  font-size: 2.2rem;
}

.footer {
  background-color: #0d0d0d;
  padding: 5rem;
  color: #f5f5dc;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.footer .contenedor-footer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.footer img {
  width: 5rem;
  height: auto;
}
.footer .navegacion-footer a {
  color: #f5f5dc;
}